import React from "react";
//import PropTypes from "prop-types";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  };
  //axiosÎäî ÏãúÍ∞ÑÏù¥ Ï¢Ä Í±∏Î¶¨Í∏∞ ÎïåÎ¨∏Ïóê async()ÏôÄ awaitÎ•º Ïç®ÏÑú ÎπÑÎèôÍ∏∞ Ï≤òÎ¶¨Î•º Ìï®
  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get(
      "http://yts-proxy.now.sh/list_movies.json?sort_by=rating"
    );
    //ÏõêÎûòÎäî setState({movies:movies})Î°ú Ïç®ÏïºÌïòÏßÄÎßå ÎëòÏù¥ Í∞ôÏïÑÏÑú ÏÉùÎûµÌï† Ïàò ÏûàÏùå
    this.setState({ movies, isLoading: false });
  };

  async componentDidMount() {
    this.getMovies();
  }

  render() {
    const { isLoading, movies } = this.state;
    return (
      //classÎùºÍ≥† ÌïòÎ©¥ javascriptÏùò classÏôÄ Ìó∑Í∞àÎ¶¨Í∏∞ ÎïåÎ¨∏Ïóê classNameÏúºÎ°ú Î∞îÍøîÏÑúÏîÄ (Ïã§Ï†úÎ°ú htmlÏóêÎäî classÎ°ú Ïù∏ÏãùÎê®)
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader_text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((movie) => (
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
                rating={movie.rating}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
